import { Component, AfterViewInit, Input, Output, EventEmitter } from '@angular/core';
import { BcHelperService } from '@bancolombia/design-system-web/bc-helpers';
import { BcPageHeaderBehavior } from '@bancolombia/design-system-behaviors';

export interface IRightButtons {
  icon: string;
  id: string;
}
export interface IRBreadcrumbs {
  text: string;
  link: string;
}

/**
 * Importa el componente BcFooter para el pie de página
 *
 * @export
 * @class BcfooterComponent
 * @implements {AfterViewInit}
 */
@Component({
  selector: 'bc-page-header',
  templateUrl: 'bc-page-header.component.html',
})
export class BcPageHeaderComponent implements AfterViewInit {
  /**
   * Id del elemento
   *
   * @type {string}
   * @memberof BcPageHeaderComponent
   */
  @Input() componentId: string = this.bcHelperService.getId(this);

  
  /**
   * Links del lado derecho
   *
   * @type {IRightButtons[]}
   * @memberof BcPageHeaderComponent
   */
  @Input() rightButtons: IRightButtons[] = [];

  /**
   * Fecha de lado derecho
   *
   * @type {string}
   * @memberof BcPageHeaderComponent
   */
  @Input() date = '';

  /**
   * Titulo de la pagina
   *
   * @type {string}
   * @memberof BcPageHeaderComponent
   */
  @Input() title = '';

  /**
   * Coloca el background blanco
   *
   * @type {boolean}
   * @memberof BcPageHeaderComponent
   */
  @Input() bgWhite = false;

  /**
   * Coloca el background blanco
   *
   * @type {boolean}
   * @memberof BcPageHeaderComponent
   */
  @Input() showBackButton = false;

  /**
   * Evento click de los botos de la derecha
   *
   * @type {EventEmitter}
   * @memberof BcPageHeaderComponent
   */
  @Output() clickButtonRight = new EventEmitter<string>();

  /**
   * Evento click para retroceder
   *
   * @type {EventEmitter}
   * @memberof BcPageHeaderComponent
   */
  @Output() clickBackButton = new EventEmitter<string>();
  bcPageHeaderBehavior;

  constructor(private bcHelperService: BcHelperService) {
    this.bcPageHeaderBehavior = new BcPageHeaderBehavior();
  }

  ngAfterViewInit() {
    this.bcPageHeaderBehavior.setId(this.componentId);
    this.bcPageHeaderBehavior.setUp();
  }

  buttonClick(idButton) {
    this.clickButtonRight.emit(idButton);
  }
}
